
import {mapActions, mapGetters} from 'vuex'

const timeScoreImg = require('~/assets/images/timeScore.png');
const flowScoreImg = require('~/assets/images/flowScore.png');
const cyclesScoreImg = require('~/assets/images/cyclesScore.png');
const welldoneImage = require('~/assets/images/finishimage.png');
const boxFromBottom = {
  targets: ".fromAbove",
  translateY: ['+10%', '0%'],
  opacity: [0, 1],
  easing: 'easeInOutQuad',
  duration: 500
};
const screenAnimation = {
  targets: ".screenAnimation",
  scaleY: ['5px', '5%'],
  scaleX: ['5px', '5%'],
  opacity: [0, 1],
  easing: 'easeInOutQuad',
  duration: 500
};
const screenAnimation2 = {
  targets: ".screenAnimation",
  scaleY: ['5px', '5%'],
  scaleX: ['5px', '100%'],
  easing: 'easeInOutQuad',
  duration: 300
};
const screenAnimation3 = {
  targets: ".screenAnimation",
  scaleY: ['5px', '100%'],
  easing: 'easeInOutQuad',
  duration: 300
};
const screenAnimation4 = {
  targets: ".screenAnimation_inner",
  opacity: [0, 1],
  easing: 'easeInOutQuad',
  duration: 300
};
const disclosureAnimation = {
  targets: ".coverImage",
  translateY: ['0%', '-100%'],
  easing: 'easeInOutQuad',
  duration: 1500
}
const circleFading = {
  targets: ".circle",
  opacity: [0, 1],
  easing: 'easeInOutQuad',
  duration: 800,
  loop: false
};
const circlesFading = {
  targets: ".circle",
  opacity: [1, 0],
  easing: 'easeInOutQuad',
  duration: 1200,
  loop: false
};


export default {
  beforeRouteLeave(to, from, next) {
    next();
  },
  data() {
    return {
      showTrend:false,
      exid: (this.$route.params.fromexercise),
      programid: (this.$route.params.fromprogram || false),
      withindex: (this.$route.params.withindex || false),
      backto: (this.$route.params.backto || false),
      scoreDialog: {
        show: false
      },
      dobDialog: {
        "focus": null,
        show: false,
        kind: "howimprove",
        currentSheet:"track",
        'graphReady': false,
        'scores': {},
        lineChartData: {
          labels: [0, 1, 2, 3, 4, 5, 6],
          datasets: [
            {
              label: '',
              data: []
            }
          ]
        },
        barChartOptions: {
          plugins: {
            legend: {
              display: false
            },
            tooltip: {
              enabled: false/*,
              position: 'nearest',
              external: externalTooltipHandler*/
            }
          },
          layout: {
            padding: {
              right: 10,
              top: 20,
              left: 0,
              bottom: 0
            }
          },
          responsive: true,
          maintainAspectRatio: false,
          pointStyle: 'circle',
          barThickness: 3,
          hoverOffset: 4,
          legend: {
            display: false,
          },
          subtitle: false,
          tooltip: false,
          scales: {
            x: {
              grid: {
                display: false
              },
              ticks: {
                display: true
              }
            },
            y: {
              min: 0,
              max: 100,
              offset: true,
              grid: {
                borderColor: 'rgba(81, 85, 112, 0.1)',
                borderWidth: 0
              },
              ticks: {
                maxTicksLimit: 3
              }
            }
          },
          elements: {
            line: {
              backgroundColor: '#848CE6',
              borderColor: '#848CE6',
              borderWidth: 3
            },
            point: {
              radius: 10,
              backgroundColor: '#515570',
              borderWidth: 2,
              borderColor: '#FFFFFF'
            }
          }
        },
        'since': '',
        'sinceobj': null,
        currentCalendarView: null,
        'events': [],
        currentDay: null,
        currentDayEvents: null,
        eventsCache: {}
      },
      breathscores: [
        {'key': "flowScore", 'active': true, 'img': flowScoreImg},
        {'key': "timeScore", 'active': false, 'img': timeScoreImg},
        {'key': "cyclesScore", 'active': false, 'img': cyclesScoreImg}
      ],
      breathscoresVoices: [
        {'key': "flowScore", 'active': true, 'img': flowScoreImg},
        {'key': "timeScore", 'active': false, 'img': timeScoreImg},
        {'key': "cyclesScore", 'active': false, 'img': cyclesScoreImg}
      ],
      currentProgram: null
    }
  },
  computed: {
    ...mapGetters({
      getExercises: 'getExercises',
      getCurrentExercise: 'getCurrentExercise',
      getCurrentUser: 'getCurrentUser',
      isCurrentUserGuest: 'isCurrentUserGuest',
      doneExercices: 'doneExercices',
      getPrograms: 'getPrograms'
    }),
    relaxingFinish() {
      return welldoneImage;
    },
    breathingScore() {
      if (!this.getCurrentExercise) {
        return 0
      }
      if (typeof this.getCurrentExercise.breathingScore === 'undefined') {
        return 0
      }
      return parseInt(this.getCurrentExercise.breathingScore);
    },
    getStaminaResult() {
      const _staminaScore = this.staminaScore;
      if(_staminaScore>0) {
        const cnt =this.$t("dob.result_"+_staminaScore);
        if(cnt.length<1 || cnt ==("dob.result_"+_staminaScore)) {
          return this.$t('dob.result_generic');
        }
      }
      return this.$t('dob.result_generic');
    },
    getStaminaTitle() {
      const _staminaScore = this.staminaScore;
      if(_staminaScore>0) {
        const cnt =this.$t("dob.title_"+_staminaScore);
        if(cnt.length<1 || (cnt == "dob.title_"+_staminaScore)) {
          return this.$t('dob.title_generic');
        }
        return cnt
      }
      return this.$t('dob.title_generic');
    },
    staminaScore() {
      if (!this.getCurrentExercise) {
        return 0
      }
      if (typeof this.getCurrentExercise.breathingServiceSoundCode === 'undefined') {
        return 0
      }
      const _soundCode =parseInt(this.getCurrentExercise.breathingServiceSoundCode);
      if(_soundCode>=10030 && _soundCode<=10037) {
        return _soundCode;
      }
       return 1;
    },
    medalScore() {
      if (!this.getCurrentExercise) {
        return 0
      }
      if (typeof this.getCurrentExercise.medal === 'undefined') {
        return 0
      }
      return parseInt(this.getCurrentExercise.medal);
    },
    breathingScoreShow() {
      if (this.getCurrentExercise.type == this.$exerciseManager.kindRECORDING) {
        //if (typeof this.getCurrentExercise.subtype !== 'undefined' && this.getCurrentExercise.subtype !== null) {
        return true;
        //}
      }
      return false;
    },
    breathingClass() {
      if (this.medalScore) {
        if (this.medalScore === 5) {
          return "medal_gotzero";
        } else if (this.medalScore === 4) {
          return "medal_improve";
        } else if (this.medalScore === 3) {
          return "medal_bronze";
        } else if (this.medalScore === 2) {
          return "medal_silver";
        } else if (this.medalScore === 1) {
          return "medal_gold";
        }
      }
      if (!this.breathingScoreShow) {
        return "medal_gold";
      }
      const iBs = this.breathingScore
      if (iBs > 50) {
        /*Hot fix with Claire ? greater or equal or only greater */
        if (iBs > 85) {
          return "medal_gold";
        }
        if (iBs > 70) {
          return "medal_silver";
        }
        return "medal_bronze";
      } else if (iBs > 0) {
        return "medal_improve";
      }
      return "medal_gotzero";
    },
    /*Track computed*/
    minCalendar() {
      if (this.dobDialog.sinceobj) {
        return this.$exerciseManager.formatDateYmd(this.dobDialog.sinceobj);
      }
      return null
    },
    maxCalendarObj() {
      let _tmp = new Date();
      return _tmp
    },
    maxCalendar() {
      return this.$exerciseManager.formatDateYmd(this.maxCalendarObj)
    },
    isPrevDisabled() {
      if (!this.dobDialog.currentCalendarView) {
        return false
      }
      if (this.isCurrentUserGuest) {
        return true;
      }
      if(!this.dobDialog.currentCalendarView) {
        return false;
      }

      let _currentMnt = this.dobDialog.currentCalendarView.getFullYear() + "_" + this.dobDialog.currentCalendarView.getMonth();
      let _mintMnt = this.dobDialog.sinceobj.getFullYear() + "_" + this.dobDialog.sinceobj.getMonth();
      if (_currentMnt === _mintMnt) {
        return true
      }
      return false
    },
    isNextDisabled() {
      if (!this.dobDialog.currentCalendarView) {
        return false
      }
      if (!this.dobDialog.maxCalendarObj) {
        return false
      }
      if (this.isCurrentUserGuest) {
        return true;
      }
      if(!this.dobDialog.currentCalendarView) {
        return false;
      }
      let _currentMnt = this.currentCalendarView.getFullYear() + "_" + this.currentCalendarView.getMonth();
      let _maxtMnt = this.dobDialog.maxCalendarObj.getFullYear() + "_" + this.dobDialog.maxCalendarObj.getMonth();
      if (_currentMnt === _maxtMnt) {
        return true
      }
      return false
    }
  },
  mounted() {
    this.dobDialog.currentCalendarView = new Date();
    this.dobDialog.sinceobj = new Date(new Date().getFullYear(),0,1);
    this.dobDialog.since = this.$t('months.short-' + this.dobDialog.sinceobj.getMonth()) + " " + this.dobDialog.sinceobj.getFullYear()

    if (this.$capacitor && this.$capacitor.isNativePlatform()) {
      this.$backEmitter.callback = this.handleBack;
    }
    if (this.getCurrentPagerHeight !== null && document.querySelector('div.finishcard > div.footer')) {
      document.querySelector('div.finishcard > div.footer').style.height = this.getCurrentPagerHeight + "px";
      document.querySelector('div.finishcard > div.footer').style.maxHeight = this.getCurrentPagerHeight + "px";
    }
    if (this.getCurrentUser && this.getCurrentUser.createdAt) {
      try {
      const _dt = new Date(this.getCurrentUser.createdAt);
      _dt.setHours(0, 0, 0, 0); //set to midnight
      if (_dt) {
        this.dobDialog.sinceobj = _dt;
        this.dobDialog.since = this.$t('months.short-' + _dt.getMonth()) + " " + _dt.getFullYear()
      }
      } catch (e) {
        console.error("ERRORE IIN SINCEOBJ", e);
        this.dobDialog.sinceobj = new Date(new Date().getFullYear(),0,1);
        this.dobDialog.since = this.$t('months.short-' + this.dobDialog.sinceobj.getMonth()) + " " + this.dobDialog.sinceobj.getFullYear()
      }
    }

    let programIDcheck = -1;
    if (this.programid !== false && this.programid != 'false' && this.programid != '-1' && this.programid != -1) {
      programIDcheck = this.programid;
    } else {
      programIDcheck = this.getCurrentExercise.program_id;
    }
    if(typeof window['currentSession'] ==='undefined') {
      window['currentSession'] = {};
    }
    //Imposta fatto per questa sessione:
    window.currentSession[this.getCurrentExercise.id + "_"+this.withindex] = true;


    const _retrieveJoinAtProgram = this.getProgramJoinedAtById(programIDcheck)
    if (_retrieveJoinAtProgram) {
      console.log('[reloading] finish start', this.getCurrentExercise);
      this.$root.$emit('showPager', {"show": false});
      this.$nextTick(() => {
        this.$root.$emit('showPager', {"show": false});
      });
      //For joined programs:
      console.log('start requesting program info')
      this.$exerciseManager.fillProgramById(programIDcheck).then((programData) => {
        console.log('received program info')
        programData.id = programIDcheck;
        //this.setProgramByData(programData);
        if (programData) {
          programData.joinedAt = _retrieveJoinAtProgram;
          //Store also the setJoined:
          if (this.hasJoinedProgramById(programData.id)) {
            this.setJoinedProgram({'localprogram': programData, 'remoteprogram': programData})
          } else {
            this.addJoinedProgram({'localprogram': programData, 'remoteprogram': programData})
          }
          this.currentProgram = programData
        }
      }).finally(() => {
        console.log('finish start [with new program readed]', this.getCurrentExercise);
        this.$root.$emit('showPager', {"show": false});
        this.$nextTick(() => {
          this.$root.$emit('showPager', {"show": false});
          this.animeWorld();
        });
        console.log('finish ready', this.getCurrentExercise);
      })
    } else {
      console.log('no joined program - finish start')
      if (programIDcheck !== false && programIDcheck != 'false' && programIDcheck != '-1' && programIDcheck != -1) {
        this.$exerciseManager.fillProgramById(programIDcheck).then((programData) => {
          console.log('received program info')
          programData.id = programIDcheck;
          //this.setProgramByData(programData);
          if (programData) {
            this.currentProgram = programData
          }
        }).finally(() => {
          console.log('[no joined]finish start [with new program readed]', this.getCurrentExercise);
          this.$root.$emit('showPager', {"show": false});
          this.$nextTick(() => {
            this.$root.$emit('showPager', {"show": false});
            this.animeWorld();
          });
          console.log('[no joined]finish ready', this.getCurrentExercise);
        })
      } else {
        this.currentProgram = this.getProgramById(this.getCurrentExercise.program_id);
        console.log('[no program]finish start [with base program checked]', this.getCurrentExercise, this.currentProgram);
        if (null == this.currentProgram) {
          console.warn("set fake program for current checks")
          this.currentProgram = {
            "_id": this.getCurrentExercise.program_id,
            "id": this.getCurrentExercise.program_id,
            "exercises": []
          };
        }
        this.$root.$emit('showPager', {"show": false});
        this.$nextTick(() => {
          this.$root.$emit('showPager', {"show": false});
          this.animeWorld();
        });
        console.log('[no program]finish ready', this.getCurrentExercise);
      }
    }
    if(this.getCurrentExercise && this.getCurrentExercise.subtype ==='dob') {
      this.showTrend = false;
      this.$nextTick(()=>{
      this.fillScoreList().then((r)=>{
          console.log('score list filled')
        });
      })
      //this.fillScoreList().then((r)=>{
      //    console.log('score list filled')
      //})
    }

    //window.testPP = this;
  },
  methods: {
    ...mapActions({
      addDoneExercise: 'addDoneExercise',
      addJoinedProgram: 'addJoinedProgram',
      setJoinedProgram: 'setJoinedProgram'
    }),
    updateActiveItem(w) {
      this.breathscores.forEach((item) => {
        if (item.key !== w) {
          item.active = false;
        } else {
          item.active = true;
        }
      });
    },
    animateCircle() {
      //fix circle height: circle
      const _circleAnimationOne = circleFading;
      const _circleAnimationTwo = circleFading;
      const _circleAnimationThree = circleFading;
      const _circleAnimation = circlesFading;
      _circleAnimation.delay = 800;
      _circleAnimation.loop = false;
      _circleAnimation.complete = function (anim) {
        circleAnime.restart();
      }
      const circleAll = this.$anime(_circleAnimation);
      _circleAnimationOne.targets = ".circle";
      _circleAnimationOne.delay = this.$anime.stagger(1500, {direction: 'reverse'});
      _circleAnimationOne.loop = false;
      _circleAnimationOne.complete = function (anim) {
        circleAll.play();
      }

      const circleAnime = this.$anime(_circleAnimationOne);
      circleAnime.play()
    },
    getProgramJoinedAtById(id) {
      return this.$nuxt.$store.getters.getProgramJoinedAtById(id)
    },
    hasJoinedProgramById(id) {
      return this.$nuxt.$store.getters.hasJoinedProgramById(id)
    },
    getProgramById(id) {
      return this.$nuxt.$store.getters.getProgramById(id)
    },
    getDoneCountByProgramData(data) {
      return this.$nuxt.$store.getters.getDoneCountByProgramData(data)
    },
    getNextExerciseByProgramData(programdata, exercise) {
      if(this.backto === "challengemode") {
          let nextExercise = this.$exerciseManager.retrieveNextExerciseByChallenge(window.currentChallenge,exercise,this.withindex)
          if(nextExercise === false) {
            //Finito...
            return false;
          } else {
            return nextExercise;
          }
      }
      return this.$nuxt.$store.getters.getNextExerciseByProgramData(programdata, exercise)
    },
    handleBack() {
      if (this.scoreDialog.show) {
        this.closeScoreDialog();
      }
      return;
    },
    closeScoreDialog() {
      this.scoreDialog.show = false
      this.dobDialog.show = false
    },
    openScoreDialog() {
      //IF dob: dobDialog
      if (this.getCurrentExercise.subtype === 'dob') {
        this.dobDialog.kind = "howimprove";
        this.dobDialog.show = true;
        return
      }

      this.scoreDialog.show = true
    },
    openTrendDialog() {
      this.dobDialog.currentCalendarView = new Date();
      this.dobDialog.kind = "checktrend";
      //Avvia richiesta grafico e mese
      this.fillSampleData().finally(() => {
        this.dobDialog.graphReady = true;
      })
      this.dobDialog.show = true;
    },
    fillSampleData() {
      this.$nuxt.$loading.start()
      if (this.isCurrentUserGuest) {
        return new Promise((resolve, reject) => {
          this.dobDialog.scores["dob"] = [];
          for (var i = 0; i < 7; i++) {
            this.dobDialog.scores["dob"].push(0);
          }
          this.reloadChartData();
          this.$nuxt.$loading.finish();
          console.log('retrieved events', this.dobDialog.events)
          resolve(true)
        });
      }
      return Promise.allSettled([
        this.fillScoreList(),
        this.fillMonthData(null)
      ]).then((p) => {
        console.log('promises finished', p)
      }).finally(() => {
        this.reloadChartData();
        this.$nuxt.$loading.finish();
        console.log('retrieved events', this.dobDialog.events)
      })

    },
    fillScoreList() {
      const headers = {
        Authorization: 'Bearer ' + window.currentVMToken,
        'api-key': window.currentVMKey,
        useCache: false
      }
      this.dobDialog.graphReady = false;

      return ["dob"].reduce((carry, score) => {
        return carry.then(() => {
          this.dobDialog.scores[score] = [];
          return this.$axios.$post($nuxt.$apiConstants.userScoreGraph + "?subtype=" + score,[], headers).then((r) => {
            if (r && r.lastSevenScores) {
              let _browseScore = [];
              if (Array.isArray(r.lastSevenScores)) {
                _browseScore = r.lastSevenScores;
              } else {
                Object.keys(r.lastSevenScores).forEach((scoreIndex) => {
                  _browseScore.push(r.lastSevenScores[scoreIndex])
                })
              }
              _browseScore.sort((a, b) => a.createdAt.localeCompare(b.createdAt, undefined, {sensitivity: 'accent'}))
              _browseScore.forEach((_score) => {
                let typ = _score.subtype || "";
                /*if (scores.indexOf(typ) > -1) {*/
                  this.dobDialog.scores[typ].push(
                    {"score": _score.breathingScore || 0, "date": this.$exerciseManager.parseJsonDate(_score.createdAt)}
                  );
                /*}*/
              })

              if(_browseScore.length>1) {
                this.showTrend =true;
              }
            }
          }).catch((e) => {
            console.error("cannot parse score list", e)
            ["dob"].forEach((score) => {
              this.dobDialog.scores[score] = [];
            })
          })
        })
      }, Promise.resolve());
    },
    fillMonthData(dateTime) {
      const _dt = dateTime || new Date();
      const _start = this.getFirstDayOfMonth(_dt);
      const _last = this.getLastDayOfMonth(_dt);
      console.log(_dt, _start, _last);
      const headers = {
        Authorization: 'Bearer ' + window.currentVMToken,
        'api-key': window.currentVMKey,
        useCache: false
      }
      const startDate = this.$exerciseManager.formatDateYmd(_start);
      const endDate = this.$exerciseManager.formatDateYmd(_last);
      const _suffixString = `?startDate=${startDate}&endDate=${endDate}&subtype=dob&type=`+this.getCurrentExercise.type;
      if (typeof this.dobDialog.eventsCache[_suffixString] !== 'undefined') {
        return new Promise((resolve, reject) => {
          resolve(true);
        });
      }

      return this.$axios.$post($nuxt.$apiConstants.userActivities + _suffixString,[], headers).then((r) => {
        console.log('got results: ', r);
        if (r && r.activitiesHistory) {
          const _parsedActvities = this.parseMonthDataResults(r.activitiesHistory);
          const _dayEvents = {};
          _parsedActvities.forEach((act) => {
            const _day = this.$exerciseManager.formatDateYmd(act.date);
            let _events = typeof _dayEvents[_day] !== 'undefined' ? _dayEvents[_day] : [];
            _events.push(act);
            _dayEvents[_day] = _events;
          });
          Object.keys(_dayEvents).map((daycontainer) => {
            let _accumulatore = _dayEvents[daycontainer];
            if (_accumulatore.length > 0) {
              const startDT = _accumulatore[0].date;
              const _monoDay = new Date(startDT.getFullYear(), startDT.getMonth(), startDT.getDate())
              const evt = {
                "name": "exercises_" + daycontainer,
                "start": _monoDay,
                "end": _monoDay,
                "color": 'blue',
                'timed': true,
                'events': _accumulatore
              }
              this.dobDialog.events.push(evt)
            }
          });
          this.dobDialog.eventsCache[_suffixString] = _dayEvents;
        }
      });
    },
    parseMonthDataResults(results) {
      let _parsedResults = [];
      results.forEach((res) => {
        const _dt = new Date(res.createdAt);
        let item = {
          "unixTime": _dt.getTime(),
          "date": _dt,
          "title": res.title,
          'time': this.$exerciseManager.formatDateHi(_dt),
          "type": res.type || 'exercise',
          "programID": res.programId || '',
          "id": res.breathingExerciseId || '',
          'score': res.breathingScore || ''
        };
        //Associa solo se lo stesso esercizio...
        if(item.id ===this.getCurrentExercise.id) {
          _parsedResults.push(item);
        }
      });
      return _parsedResults.sort((a, b) => {
        if (a.unixTime > b.unixTime) {
          return -1;
        }
        if (b.unixTime > a.unixTime) {
          return 1;
        }
        return 0;
      });
    },
    getFirstDayOfMonth(date) {
      return new Date(date.getFullYear(), date.getMonth(), 1);
    },
    getLastDayOfMonth(date) {
      return new Date(date.getFullYear(), date.getMonth() + 1, 0);
    },
    reloadChartData() {
      this.dobDialog.graphReady = false;
      new Promise((resolve, reject) => {
        let data = this.dobDialog.scores["dob"];
        if (typeof data === 'undefined') {
          let tmp = {
            labels: labels,
            datasets: [
              {
                label: '',
                data: []
              }
            ]
          };
          this.$set(this.dobDialog, 'lineChartData', tmp)
          resolve(true)
          return;
        }
        console.log('request chart data', data);
        let labels = [];
        for (let i = 0; i < data.length; i++) {
          const dt = data[i]
          if (typeof dt === 'object') {
            const formatDate = this.$exerciseManager.formatDateMonthDay(dt.date);
            labels.push(formatDate);
          } else {
            labels.push(i);
          }
        }
        let tmp = {
          labels: labels,
          datasets: [
            {
              label: '',
              data: data.map((dt) => {
                if (typeof dt === 'object') {
                  return dt.score
                } else {
                  return dt
                }
              })
            }
          ]
        };
        this.$set(this.dobDialog, 'lineChartData', tmp)
        resolve(true)
      }).finally(() => {
        console.log('lineData ready', this.dobDialog.lineChartData);
        this.dobDialog.graphReady = true;
      })
    },
    openInfoDialog() {
      this.dobDialog.kind = "whatiscardio";
      this.dobDialog.show = true;
    },
    getRunningProgramDoneCount() {
      if (this.currentProgram) {
        return this.getDoneCountByProgramData(this.currentProgram)
      }
      return 0
    },
    pickProgram(item) {
      $nuxt.$router.push($nuxt.localePath("/ml/programs/" + item.id));
    },
    pickProgramExercise(item) {
      console.log('picked program exercise', item);
      $nuxt.$router.push($nuxt.localePath("/ml/exercises/" + item.id + "/" + item.program_id + "/" + item.program_index+"/"+this.backto));
    },
    moveNextProgram(program) {
      $nuxt.$router.push($nuxt.localePath("/ml/programs/" + program.id));
    },
    formatSeconds(seconds) {
      return this.$humanizeTimeNoSuffix(seconds);
    },
    animeWorld() {
      this.$analytics.logEvent('exercise_completed', {
        "id": this.getCurrentExercise.id,
        "name": this.getCurrentExercise.title,
        "program_id": this.getCurrentExercise.program_id || null,
        "type": this.getCurrentExercise.type,
      })
      const cardAnimation = boxFromBottom;
      const inCardAnime1 = screenAnimation;
      const inCardAnime2 = screenAnimation2;
      const inCardAnime3 = screenAnimation3;
      const inCardAnime4 = screenAnimation4;
      let coverAnimation = disclosureAnimation;
      cardAnimation.delay = this.$anime.stagger(300, {start: 200});
      //inCardAnime1.delay = this.$anime.stagger(300, {start: 200});

      cardAnimation.autoplay = false;
      coverAnimation.delay = 500;
      let subAnimPlay = false;
      const cardAnims = this.$anime(cardAnimation);
      const inCardAnimes = this.$anime.timeline({
        easing: 'easeOutExpo',
        duration: 750,
        autoplay: false
      });
      inCardAnimes.delay = this.$anime.stagger(300, {start: 200});
      inCardAnimes.add(inCardAnime1);
      inCardAnimes.add(inCardAnime2);
      inCardAnimes.add(inCardAnime3);
      inCardAnimes.add(inCardAnime4);

      coverAnimation.update = (anim) => {
        if (anim.progress >= 60 && subAnimPlay === false) {
          subAnimPlay = true;
          [...document.querySelectorAll('.faded')].map((i) => i.classList.remove('faded'));
          cardAnims.play()
          inCardAnimes.play();
        }
      }
      this.$anime(coverAnimation);
      this.animateCircle();

    },
    ensureProgramCompleted() {
      if (this.currentProgram.exercises.length === this.getDoneCountByProgramData(this.currentProgram)) {
        console.log('Challenge completed... mark as completed out side the challenge page')
        this.$exerciseManager.programCompleteCurrent(this.currentProgram.id).then((r) => {
          console.log('Sent program complete', r)
        }).catch((e) => {
          console.error("Cannot send program complete", e)
        })
      }
    },
    exerciseStepBack() {



      if (this.backto == 'standalone' || this.backto == 'challengemode') {
        let result = {
          "medal": this.medalScore
        }
        this.breathscores.forEach((obj) => {
          if (typeof this.getCurrentExercise[obj.key] !== 'undefined') {
            result[obj.key] = this.getCurrentExercise[obj.key];
          }
        });
        this.breathscoresVoices.forEach((obj) => {
          if (typeof this.getCurrentExercise[obj.key] !== 'undefined') {
            result[obj.key] = this.getCurrentExercise[obj.key];
          }
        });

        if(this.backto ==='challengemode') {
          const _nextExercise = this.getNextExerciseByProgramData(window.currentChallenge,this.getCurrentExercise)
          if(_nextExercise!==false) {
            console.log('muovi al prossimo esercizio',_nextExercise);
            window.currentExercise = _nextExercise;
            this.pickProgramExercise(_nextExercise);
            return;
          }
        }
        /*
        $nuxt.$airlyn.finishExercise({"reason": 'exercise-completed', "result": result}).finally(() => {
          console.log('exercise completed, success')
        });
         */
        $nuxt.$airlyn.closeExercise().finally(() => {
          console.log('exercise closed, success')
        });
      }



      if (this.backto == 'today') {
        this.ensureProgramCompleted()
        $nuxt.$router.replace($nuxt.localePath("/user/today"));
        return
      }
      if (this.backto == 'discovery') {
        this.ensureProgramCompleted()
        $nuxt.$router.replace($nuxt.localePath("/home"));
        return
      }
      if (this.programid) {
        if (this.programid == "-1" || this.programid == -1 || this.programid == "false" || this.programid == false) {
          $nuxt.$router.replace($nuxt.localePath("/user/today"));
        } else {
          $nuxt.$router.replace($nuxt.localePath("/ml/programs/" + this.programid));
        }
        return
      }
      this.ensureProgramCompleted()
      $nuxt.$router.replace($nuxt.localePath("/user/today"));
    },
    buildSuffix() {
      let suffixes = [];
      suffixes.push(this.programid ? this.programid : false)
      suffixes.push(this.withindex ? this.withindex : "-1")
      suffixes.push(this.backto ? this.backto : false)
      return suffixes.join("/")
    },
    openRegister() {
      //Vai a registrati...
      console.warn("user is guest, move to register");
      this.$useractions.logout().then((r) => {
        console.log('logged out', r);
        this.setRunningProgram(null)
        this.setCurrentUser({})
        this.setIsGuest(false)
        this.$axiosService.$clearCache();
      }).catch((r) => {
        console.error('cannot logout', r);
      }).finally(() => {
        $nuxt.$router.push($nuxt.localePath("/user/signup") + "/0");
      });
      console.log('devi essere autenticato...');
    },
    contactUsStep() {
      //OPen : https://airlyn.io/contact-us/
      this.$browser.open({url: "https://airlyn.io/contact-us/"});
    },
    prevStep() {
      console.log('got something about back button?');
    },
    quitStep() {
      this.exerciseStepBack()
    },
    backStep() {
      let suffix = this.buildSuffix();
      if (suffix.length > 0) {
        suffix = "/" + suffix
      }
      if (this.exid) {
        let result = {}
        $nuxt.$airlyn.closeExercise().finally(() => {
          console.log('exercise closed, success')
        });
        this.$router.push($nuxt.localePath("/ml/exercises/" + this.exid + suffix));
      } else {
        this.exerciseStepBack()
      }
      this.$gtag('event', 'click', {
        'event_label': 'Redo',
        'event_category': 'Exercises',
      })
    },
    nextStep() {
      if (this.hasJoinedProgramById(this.programid) && this.getNextExerciseByProgramData(this.currentProgram, this.getCurrentExercise) && !this.isCurrentUserGuest) {
        this.pickProgramExercise(this.getNextExerciseByProgramData(this.currentProgram, this.getCurrentExercise));
        return;
      }
      /* Deprecated
      if (this.getRunningProgram && this.getNextProgramExercise && !this.isCurrentUserGuest) {
        this.moveNextProgram(this.getNextProgramExercise);
        return;
      }
       */
      if (document.querySelector('#__nuxt')) {
        document.querySelector('#__nuxt').classList.add('quit');
      }
      this.$gtag('event', 'click', {
        'event_label': 'Done',
        'event_category': 'Exercises',
      })
      this.exerciseStepBack()
    },
    /*Trend bar*/
    formatDay(Date) {
      let _string = this.$t('weeknames.long-' + Date.getDay()) + ", " +
        Date.getDate() + " " + this.$t('months.short-' + Date.getMonth());
      return _string;
    },
    isCurrentDay(year, month, day) {
      if (this.dobDialog.currentDay) {
        const _dt = new Date(year, month - 1, day);
        if (_dt.getTime() == this.dobDialog.currentDay.getTime()) {
          return "active";
        }
    }
      return "";
    },
    picksingleDate(year, month, day) {
      this.pickedDate({year: year, month: month, day: day})
    },
    isDisabledDay(day, year, month) {
      const _dt = new Date(year, month - 1, day)
      if (this.dobDialog.maxCalendarObj && _dt.getTime() > this.dobDialog.maxCalendarObj.getTime()) {
        return true;
      } else if (this.dobDialog.sinceobj && _dt.getTime() < this.dobDialog.sinceobj.getTime()) {
        return true;
      }
      return false;
    },
    calPrev() {
      this.$refs.calendar.prev()
    },
    calNext() {
      this.$refs.calendar.next()
    },
    pickedDate(date) {
      this.dobDialog.currentDay = new Date(date.year, date.month - 1, date.day);
      const tmp = this.dobDialog.events.find((v) => v.start.getTime() === this.dobDialog.currentDay.getTime());
      if (tmp) {
        this.dobDialog.currentDayEvents = tmp.events;
      } else {
        this.$set(this.dobDialog, 'currentDayEvents', [])
      }
    },
    returnMonth(dayTime) {
      //#month-format
      return "";
    },
    returnDay(weekDay) {
      return weekDay.day;
    },
    returnWeekName(weekDay) {
      return this.$t('weeknames.ushort-' + weekDay.weekday);
    },
    updateRange({start, end}) {
      console.log('range changes', start, end)
      const dt = new Date(start.year, start.month - 1, start.day)
      this.dobDialog.currentCalendarView = dt;
      this.fillMonthData(dt)
    },
  }
}
